*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 600px;
    /* background-image: url('../images/1.jpg'); */
    background-color: black;
}

.searchBar{
    display: flex;
    margin-left: 170px;
}

.weatherBody{
    background-color: white;
}

.weatherImage{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    width: 600px;
}

.weatherImage{
    padding: 20px 0px;
    font-size: 20px;
    font-weight: 500;
}

.img{
    margin-left: 10px;
    margin-right: 20px;
}

.weatherStatus{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #00ced1;
}


.bgBlack{
    background-color: black;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    padding: 20px 52px;
    margin-left: -57px;
}

.temp{
    margin-right: 50px;
    font-size: 35px;
}

.status{
    font-size: 35px;
}

.place{
    font-size: 20px;
}

.weatherInfo{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    padding: 30px 10px;
    font-size: 15px;
    font-weight: 500;
}

.datetime{
    font-size: 20px;
    font-weight: 500;
    text-align: center;
}

.weatherFeel{
    text-align: center;
    font-size: 19px;
    font-weight: 500;
}

.tempMin, .tempMax, .tempMinIcon, .tempMaxIcon {
    margin-top: 40px;
}



/* responsive css  */
@media only screen and (max-width: 500px) {
    #bgBlack{
        margin-left: 0px;
    }
    #searchBar {
        margin-left: 80px;
    }
    .weatherImage{
        width: 400px;
    }
}

